import MenuItems from './MenuItems';
import ExtraMenuItem from './ExtraMenuItem';
import SidebarToggler from './SidebarToggler';

export default {
  name: 'Sidebar',
  components: {
    MenuItems,
    ExtraMenuItem,
    SidebarToggler,
  },
  // mixins: [status],
  computed: {
    status() {
      return this.$store.state.admin.status;
    },
    extraMenuItem() {
      return this.$store.state['admin/extraMenuItem'];
    },
    menuItems() {
      return this.$store.getters['admin/leftMenu'];
    },
    miniMenu() {
      return this.status.miniMenu;
    },
  },
  data() {
    return {
      drawer: true,
      fixed: false,
      tabs: null,
    };
  },
  created() {
    console.log('created', this.$store);
  },
};
