/* eslint no-underscore-dangle: ["error",{"allow":["_id"]}] */

import DataTable from '../components/admin/DataTable';

export default {
  name: 'Projects',
  data() {
    return {
      showDialog: false,
      formValid: false,
      id: '',
      name: '',
      nameRules: [
        v => !!v || 'Название обязательно',
      ],
      url: '',
      urlRules: [
        v => !!v || 'URL адрес обязятелен',
      ],
      disableInput: false,
      modalTitle: 'Добавить новый проект',
      modalSubmitButton: 'Добавить',
      modalAction: '',
    };
  },
  components: {
    DataTable,
  },
  methods: {
    sendRequest() {
      this.$store.dispatch('loadProjects');
    },
    clickItem(item) {
      console.log('clicked on', item);
      // this.$store.dispatch('openProject', item._id);
      document.location.href = `/projects/${item._id}`;
    },
    addItem() {
      this.modalTitle = 'Добавить новый проект';
      this.modalSubmitButton = 'Добавить';
      this.modalAction = 'Add';
      this.name = '';
      this.url = '';
      this.disableInput = false;
      this.showDialog = true;
    },
    editItem(item) {
      this.modalTitle = 'Редактировать проект';
      this.modalSubmitButton = 'Сохранить';
      this.modalAction = 'Edit';
      this.id = item._id;
      this.name = item.name;
      this.url = item.url;
      this.disableInput = false;
      this.showDialog = true;
    },
    deleteItem(item) {
      this.modalTitle = 'Удалить проект';
      this.modalSubmitButton = 'Удалить';
      this.modalAction = 'Delete';
      this.id = item._id;
      this.name = item.name;
      this.url = item.url;
      this.disableInput = true;
      this.showDialog = true;
    },
    backupItem(item) {
      this.modalTitle = 'Сделать бэкап проекта';
      this.modalSubmitButton = 'OK';
      this.modalAction = 'Backup';
      this.id = item._id;
      this.name = item.name;
      this.url = item.url;
      this.disableInput = true;
      this.showDialog = true;
    },
    confirmModalAction() {
      const action = this.modalAction;
      switch (action) {
        default:
          break;
        case 'Add':
          this.addProject();
          break;
        case 'Edit':
          this.saveProject();
          break;
        case 'Delete':
          this.deleteProject();
          break;
        case 'Backup':
          this.backupProject();
          break;
      }
    },
    addProject() {
      console.log('Проект добавлен', this.name, this.url);
      this.$store.dispatch('addProject', { name: this.name, url: this.url, status: { code: 'NOT UPDATED' } });
      this.showDialog = false;
      this.sendRequest();
    },
    deleteProject() {
      console.log('Проект удалён', this.name, this.url, this.id);
      this.$store.dispatch('deleteProject', this.id);
      this.showDialog = false;
      this.sendRequest();
    },
    saveProject() {
      console.log('Проект сохранен', this.id, this.name, this.url);
      this.$store.dispatch('saveProject', { name: this.name, url: this.url, id: this.id });
      this.showDialog = false;
      this.sendRequest();
    },
    backupProject() {
      console.log('Запрос на создание бэкапа добавлен в очередь');
      this.$store.dispatch('backupProject', this.id);
      this.showDialog = false;
    },
  },
  computed: {
    controls() {
      return this.$store.state.ui.defaultControls;
    },
    isShow() {
      return true;
    },
    items() {
      return this.$store.getters.projects;
    },
    totalItems() {
      return this.$store.getters.projects;
    },
    headers() {
      return [{ text: 'Название', value: 'name' }, { text: 'URL', value: 'url', sortable: false }];
    },
    /**
     * преобразует значение по ключу заголовка (headers)
     * @example headerKey: function(value) { return transform (value); }
     */
    transforms() {
      return {
        updateAt(value) {
          return value.toString();
        },
      };
    },
  },
  created() {
    this.sendRequest();
  },
  mounted() {
    document.addEventListener('keydown', e => {
      if (this.showDialog === true && e.keyCode === 27) {
        this.showDialog = false;
      } else if (this.showDialog === true && this.formValid === true && e.keyCode === 13) {
        this.confirmModalAction();
      }
    });
  },
};
